import React, { useState} from 'react'
import MapContainer from './MapContainer'
import CalculateHeader from './CalculateHeader'
import {CalculateContainer, CalculateSidebar, PlaceTitle, PlaceDescription} from '../styles'
import FriendContainer from './FriendContainer'


const Calculate = (props) => {
    const [addr, setAddr] = useState([])
    return(
    <>
        <CalculateHeader></CalculateHeader>
        <CalculateContainer>
            <CalculateSidebar>
            {/* {props.places.map(({ place_id, formatted_address, geometry: { location } }) =>
                <li key={place_id}>
                {formatted_address}
                {" at "}
                ({location.lat()}, {location.lng()})
                </li>
            )} */}
                <PlaceTitle style={{fontSize: '36px'}}>{JSON.parse(props.location.data).name}</PlaceTitle>
                {console.log(props.location.data)}
                <PlaceDescription> {JSON.parse(props.location.data).formatted_address}</PlaceDescription>
                <FriendContainer form={addr} setForm={setAddr}></FriendContainer>
            </CalculateSidebar>
        <MapContainer center={{lat: JSON.parse(props.location.data).geometry.location.lat, 
                lng: JSON.parse(props.location.data).geometry.location.lng}} isMarkerShown={true}></MapContainer>
        </CalculateContainer>

    </>
    )
}
export default Calculate