import React, {useState, useReducer} from 'react'
import {nanoid} from 'nanoid'
import { FriendTitle, AddButton, FriendItem, FriendListStyle, FriendContainerStyle, FriendName, DeleteButton} from '../styles'
import SearchBoxContainer from './Searchbox'
import DeleteImg from '../images/delete.png'
import { parseSync } from '@babel/core'

//based on personal-assistant, spread operator includes+adds each item in list
const formReducer = (prevState, action) => {
  if(!action){
    return { ...prevState }
  }
  switch(action.type){
    case 'NEW_NAME':
      var i;
      for (i = 0; i < prevState['friends'].length; i++) {
          if(prevState['friends'][i].id === action.id)
            prevState['friends'][i].name = action.name
      }
      return prevState
    case 'NEW_ADDRESS':
        var i;
        for (i = 0; i < prevState['friends'].length; i++) {
            if(prevState['friends'][i].id === action.id)
              prevState['friends'][i].address = action.address
        }
        return prevState
    default:
      return { ...prevState, ...action.payload }
  }
}

const FriendContainer = ({addr, setAddr}) => {
  const [form, setForm] = useReducer(formReducer, {friends: []})

  return(
    <FriendContainerStyle>
    <div style={{display:"flex", justifyContent:"space-between"}}>
    <FriendTitle>Friends</FriendTitle>
    <AddButton onClick={()=>setForm(form['friends'].push({id:nanoid(), name: "Sample Name", address: "Enter an address..."}))}>+</AddButton> 
    </div>
    {form.friends.length>0? <FriendList 
        // t={ form['tasks'].filter(task => task.text.includes(searchText))} use later when we have categories
        form={form} setForm={setForm}>
        </FriendList> : <NoFriends/>}
    </FriendContainerStyle>
  )
}

const FriendList = ({form, setForm}) => (
    <FriendListStyle>
        {form['friends'].map((task) => 
      <Friend key={task.id} {...task} form={form} setForm={setForm} />)}
    </FriendListStyle>
)

const Friend = ({id, name, address, form, setForm}) => {
    //const [places, setPlaces] = useState("");
    return(
      <FriendItem>
          <FriendName>{name}</FriendName> 
          <SearchBoxContainer value={form} setValue={(newPlace) => setForm(form, {type: "NEW_ADDRESS", id: id, address: newPlace})} 
            style={{height: "25px", padding: "0 0 0 20px", backgroundPosition: "0px", backgroundSize: "13px"}}>
            </SearchBoxContainer>
            {console.log(form)}
            <div style={{display:"flex", justifyContent:"space-between", padding:"0 5px 0 0"}}>
                <DeleteButton>
                <img src={DeleteImg} alt="Delete" style={{width: "15px"}} onClick={()=>setForm({friends: form['friends'].filter(friend => friend.id !== id)})}></img>
                </DeleteButton>
                <FriendName style={{margin: "-4px 0 0 0", fontSize: "15px"}}>Time</FriendName>
            </div>
      </FriendItem>
    )
  }

const NoFriends = () => (
<div>
  Add some friends! :)
</div>
)

export default FriendContainer