import React from 'react'
import {Link} from 'react-router-dom'
import {HeaderList, NavBarItem} from '../styles'
import BackArrow from '../images/white_back_arrow.png'

const CalculateHeader = () => (
        <HeaderList>
            <NavBarItem><Link to="/" style={{color:"white", textDecoration:"none"}}><img src={BackArrow} style={{width:'30px'}} alt="Back" />Back to Home</Link></NavBarItem>
        </HeaderList>
)

export default CalculateHeader